
html,
body {
margin: 0;
padding: 0;
background-color: #e5e5e5;
background-image: linear-gradient(315deg, #dbdbdb 0%, #f5f7fa 74%);
font-family: sans-serif;
}


canvas {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}



.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}


a {
  text-decoration: none;
  color: #000000;
  font-size: 0.8em;
  font-family: sans-serif;
  padding:0.2em;
}
 


