* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
}

#root {
  overflow: auto;
}


.content {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.ui {
  position: absolute;
  z-index: 500;
  color: white !important;
}

.title {
  font-size: 1em;
  /* padding: 0.5rem 1rem; */
  margin-left: 3em;
  font-family:sans-serif;
  color:navy
}

.loading {
  padding: 10px;
  font-weight: 400;
  font-size: 2.5em;
  transform: translate3d(-50%, -50%, 0);
  color: #ffffff;
}



/*MAIN CSS FOR LABELS*/

.overlay {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size:0.83em;
  font-family: sans-serif;
  cursor: pointer;
  width:7em;
  /*
  height:10em; */
  /* margin-top:-5em;
  margin-left:-9em; */
  background-color:rgba(190, 190, 190, 0.308);
}




.circle {
  width: 1.5em;
  height: 1.5em;
  border-radius: 250px;
  font-size: 0.9em;
  font-weight: 800;
  color: #1e2886;
  text-align: center;
  background: #ffffff;
  cursor: pointer !important;
  background-color:rgba(255, 255, 255, 0);
  opacity: 0.5
}

.text-overlay {
  font-size: 0.8em;
  padding: 0 !important;
  padding-left: 2px !important;
  color: black;
  text-align: left;
  cursor: pointer;
  opacity: 0.5
}


